 [class^="i-"]:before, [class*=" i-"]:before {
  font-family: "the-word-is-util";
  font-style: normal;
  font-weight: normal;
  speak: none;
 
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */
 
  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;
     
  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;
 
  /* remove if not needed */
 
  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */
 
  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}
.i-play:before { content: '\e800'; padding-left:3px; }
.i-stop:before { content: '\e801'; }
.i-fast-fw:before { content: '\e802'; padding-left:5px; }
.i-spinner:before { content: '\e834'; margin-left: .2em;}


